/* 
================================================================================ END OF SETUP/NORMALIZE
*/

/* 
================================================================================ GLOBAL
*/

html { font-size: 62.5%; }

h1,
h2,
h3,
p,
ul,
li,
fieldset {
  padding: 0;
  margin: 0;
}

fieldset {
  border: none;
}

ul {  list-style: none; }


input[type="checkbox"].sr-only:focus + label { outline: 2px solid blue; }
/* input[type="checkbox"].sr-only:focus + label { outline: 2px solid blue; } */
/* input[type="checkbox"].sr-only + label:focus { outline: 2px solid blue; } */


@media (hover:hover) {
  button {
    cursor: pointer;
  }

  input[type="checkbox"].sr-only + label {
    cursor: pointer;
  }
}


/* 
================================================================================ LAYOUT
*/

.pageContainer {
  min-height: 100vh;
  position: relative;
}

.wrapper {
  max-width: 1440px;
  width: 90%;
  margin: 0 auto;
}


/* 
================================================================================ TYPOGRAPHY
*/

body {
  /* Fonts taken from index.css */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 1.6rem;
  background-color: var(--color-dark);
}

h1 {
  font-size: calc(2rem + 2vw);
  letter-spacing: 1px;
}

h2 { 
  font-size: 2.2rem;
  font-weight: 400; 
}

h3 {
  font-size: 1.8rem;
  font-weight: 400;
}

button {
  letter-spacing: inherit;
  font-family: inherit;
}

.btn.btn--toggleMenu span {
  font-size: 2rem;
}

/* 
================================================================================ VARIABLES
*/
:root {
  --color-light: #FAFAFA;
  --color-lightgrey: #EEE;
  --color-lightgrey-2: #CCC;
  --color-darkgrey: #333;
  --color-dark: #111;

  --color-red: rgb(178, 34, 34);
  --color-red-a: rgba(178, 34, 34, 0.8);
  --color-lightred: rgb(255, 127, 80);
  --color-lightred-a: rgb(255, 127, 80, 0.8);

  --color-green: rgb(0, 100, 0);
  --color-green-a: rgb(0, 100, 0, 0.8);
  --color-lightgreen: #4ecca3;
  --color-lightgreen-a: rgba(78, 204, 163, 0.8);

  --color-black: rgb(17, 17, 17);
  
  --color-blue: rgb(30, 55, 153);
  --color-blue-a: rgba(30, 55, 153, 0.8);
  --color-lightblue: #59A2FF;
  --color-lightblue-a: rgba(89, 162, 255, 0.8);

  --boxShadow: 0 2px 3px rgba(17,17,17,0.4);
}

/* 
================================================================================ COMPONENTS
*/

/* ------------------------------------ BUTTONS */
.btn {
  font-size: 1.6rem;
  color: var(--color-light);
  border: 2.4px solid transparent;
  border-radius: 5px;
  padding: 6px;
  transition: border-color 0.3s, filter 0.3s;
}

.dark .btn {
  color: var(--color-dark);
}

.btn:active,
.btn:focus {
  cursor: pointer;
  border-color: var(--color-dark);
}

.dark .btn:focus {
  border-color: var(--color-light);
}

.btn--black:active,
.btn--black:focus {
  border-color: var(--color-red);
}

.btn:disabled { opacity: 0.4; }

.btn--black { background-color: var(--color-dark); }
.dark .btn--black { background-color: var(--color-lightgrey); }

.btn--blue { background-color: var(--color-blue); }
.dark .btn--blue { background-color: var(--color-lightblue); }

.btn--green { background-color: var(--color-green); }
.dark .btn--green { background-color: var(--color-lightgreen); }

.btn--red { background-color: var(--color-red); }
.dark .btn--red { background-color: var(--color-lightred); }

.btn.btn--toggleMenu {
  background: none;
  border: none;
  color: inherit;
}

.btn--theme {
  background-color: transparent;
  border-color: var(--color-dark);
  color: var(--color-dark);
}
.dark .btn--theme {
  border-color: var(--color-light);
  color: var(--color-light);
}

@media (hover:hover) {
  .btn:hover { 
    cursor: pointer;
    filter: brightness(1.1);
  }
  
  .btn:disabled:hover { cursor: not-allowed;}
}
/* 
================================================================================ PAGE CONTAINER
*/

.pageContainer {
  background-color: var(--color-lightgrey);
  color: var(--color-dark);
}

.pageContainer.dark {
  background-color: var(--color-darkgrey);
  color: var(--color-lightgrey);
}

/* 
================================================================================ HEADER
*/

header {
  color: var(--color-dark);
  padding: 2rem 0;
  background-color: var(--color-light);
  box-shadow: var(--boxShadow);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.dark header {
  color: var(--color-lightgrey);
  background-color: var(--color-dark);
}

header .displayContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.header__text {
  margin-bottom: 1rem;
  font-weight: 300;
}

.header__buttons {
  text-align: right;
  position: relative;
}

.header__buttons .displaySettings__label {
  padding: 10px;
}

/* 
================================================================================ HEADER > MENU
*/
.menu {
  position: absolute;
  z-index: 1000;
  top: calc(100%);
  right: 0;
  width: 220px;
  display: flex;
  flex-direction: column;
  background-color: var(--color-light);
  color: var(--color-dark);
  border: 2px solid var(--color-dark);
  text-align: center;
  padding: 40px 20px;
}

@media (max-width: 480px) {
  .menu {
    right: 50%;
    transform: translateX(50%);
  }
}

.dark .menu {
  background-color: var(--color-dark);
  color: var(--color-lightgrey);
  border-color: var(--color-lightgrey);

}

.menu .themeToggle__label {
  padding: 5px;
}

.menu .themeToggle__label span { 
  margin-left: 5px;
}
.menu > *:not(:last-child) {
  margin-bottom: 20px;
}

/* 
================================================================================ MAIN
*/

main { padding: 2rem 0 12rem; }

/* 
================================================================================ MAIN > TASKBOARD MENU
*/

.taskBoard__menu {
  background-color: var(--color-light);
  border-radius: 5px;
  margin-bottom: 2rem;
  padding: 2rem;
  box-shadow: var(--boxShadow);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dark .taskBoard__menu {
  background-color: var(--color-dark);
}

/* ------------------------------------ MAIN > TASKBOARD MENU > LIST FILTER */

.inputContainer__filter {
  display: flex;
  align-items: baseline;
  justify-content: center;
}

.inputContainer__filter label { 
  opacity: 0.4;
  transition: opacity 0.3s, border-color 0.3s;
}
.inputContainer__filter label:not(:last-of-type) { margin-right: 1rem; }

.taskBoard__filterInput:checked + label,
.taskBoard__filterInput:checked:hover + label { 
  opacity: 1; 
}

.taskBoard__filterInput:hover + label { 
  border-color: transparent;
  opacity: 1;
}


/* ------------------------------------ MAIN > TASKBOARD MENU > SEARCH BAR */
.inputContainer__searchBar {
  position: relative;
  display: inline-block;
  width: 100%;
  max-width: 320px;
}

.taskBoard__searchBar {
  background-color: var(--color-light);
  border: 2px solid var(--color-dark);
  border-radius: 5px;
  padding: 6px 30px 6px 6px;
  width: 100%;
}

.dark .taskBoard__searchBar {
  background-color: var(--color-darkgrey);
  color: var(--color-lightgrey);
  border-color: var(--color-lightgrey);
}

.dark .taskBoard__searchBar::placeholder {
  color: inherit;
}

.taskBoard__searchBarIcon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.taskBoard__searchBarBtn {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  padding: 0 10px;
  color: var(--color-light);
  border: none;
  background-color: var(--color-dark);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.dark .taskBoard__searchBarBtn {
  background-color: var(--color-lightgrey);
  color: var(--color-dark);
}

.taskBoard__searchBarBtn:hover,
.taskBoard__searchBarBtn:focus {
  cursor: pointer;
}

/* 
================================================================================ MAIN > TASK LISTS
*/

.taskLists {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  column-gap: 4rem;
  row-gap: 2rem;
}

/* ------------------------------------ MAIN > TASK LISTS > TASK LIST HEADER */
.taskList__header {
  margin-bottom: 1.5rem;
  border-radius: 5px;
  background-color: var(--color-light);
  padding: 1.5rem 1.5rem 1.5rem 3.5rem;
  box-shadow: var(--boxShadow);
  display: grid;
  grid-template-columns: 1fr 10rem;
  position: relative;
  overflow: hidden;
}

.dark .taskList__header {
  background-color: var(--color-dark);
}

.taskList__menuBtn {
  border: none;
  border-radius: 0;
  background: none;
  position: absolute;
  padding: 10px 8px 10px 12px;
  top: 0;
  left: 0;
  bottom: 0;
  transition: color 0.3s, background-color 0.3s;
}

.taskList__headingText { 
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.taskList__count { margin-left: 1rem; }

.taskList__addBtn { justify-self: end; }

.taskList__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  gap: 1.5rem;
}


/* -------------- MAIN > TASK LISTS > TASK LIST HEADER -- OPEN */
.taskList__header--open { border-bottom: 5px solid var(--color-red); }
.dark .taskList__header--open { border-bottom: 5px solid var(--color-lightred); }

.taskList__header--open .taskList__count,
.taskList__header--open .taskList__menuBtn {
  color: var(--color-red);
}

.dark .taskList__header--open .taskList__count,
.dark .taskList__header--open .taskList__menuBtn {
  color: var(--color-lightred);
}

.taskList__header--open .taskList__addBtn { background: var(--color-red);}
.dark .taskList__header--open .taskList__addBtn { background: var(--color-lightred);}

/* -------------- MAIN > TASK LISTS > TASK LIST HEADER -- IN PROGRESS */
.taskList__header--inProgress { border-bottom: 5px solid var(--color-blue); }
.dark .taskList__header--inProgress { border-bottom: 5px solid var(--color-lightblue); }

.taskList__header--inProgress .taskList__count,
.taskList__header--inProgress .taskList__menuBtn {
  color: var(--color-blue);
}
.dark .taskList__header--inProgress .taskList__count,
.dark .taskList__header--inProgress .taskList__menuBtn {
  color: var(--color-lightblue);
}

.taskList__header--inProgress .taskList__addBtn { background: var(--color-blue); }
.dark .taskList__header--inProgress .taskList__addBtn { background: var(--color-lightblue); }

/* -------------- MAIN > TASK LISTS > TASK LIST HEADER -- COMPLETE */
.taskList__header--complete { border-bottom: 5px solid var(--color-green); }
.dark .taskList__header--complete { border-bottom: 5px solid var(--color-lightgreen); }

.taskList__header--complete .taskList__count,
.taskList__header--complete .taskList__menuBtn {
  color: var(--color-green);
}
.dark .taskList__header--complete .taskList__count,
.dark .taskList__header--complete .taskList__menuBtn {
  color: var(--color-lightgreen);
}

.taskList__header--complete .taskList__addBtn { background: var(--color-green); }
.dark .taskList__header--complete .taskList__addBtn { background: var(--color-lightgreen); }

/* -------------- MAIN > TASK LISTS > TASK LIST HEADER -- MENU BUTTON ACTIVE STATE */
.taskList__header .taskList__menuBtn--active { 
  background-color: var(--color-dark); 
  color: var(--color-light);
}

.dark .taskList__header .taskList__menuBtn--active { 
  background-color: var(--color-lightgrey); 
}

/* 
================================================================================ MAIN > TASK ITEMS
*/

.taskItem {
  padding: 2rem 5rem;
  border-radius: 5px;
  position: relative;
  background-color: var(--color-light);
  min-height: 12rem;
  overflow: hidden;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.dark .taskItem {
  background-color: var(--color-dark);
}

.taskItem__text {
  width: 100%;
  text-align: left;
  padding: 0 0 2rem;
  white-space: pre-line;
  border: none;
  background: none;
}

.dark .taskItem__text {
  color: var(--color-light);
}

.taskItem__text:hover { cursor: pointer; }

.taskItem--open { box-shadow: 0 2px 3px var(--color-red-a); }
.dark .taskItem--open { box-shadow: 0 2px 3px var(--color-lightred-a); }

.taskItem--inProgress { box-shadow: 0 2px 3px var(--color-blue-a); }
.dark .taskItem--inProgress { box-shadow: 0 2px 3px var(--color-lightblue-a); }

.taskItem--complete { box-shadow: 0 2px 3px var(--color-green-a); }
.dark .taskItem--complete { box-shadow: 0 2px 3px var(--color-lightgreen-a); }

.taskItem__btn {
  padding: 0 18px;
  border: 0;
  background-color: transparent;
  position: absolute;
  top: 0;
  bottom: 0;
  transition: background-color 0.3s;
  color: inherit;
}

.taskItem__btn:hover { background-color: var(--color-lightgrey-2); }
.dark .taskItem__btn:hover { color: var(--color-dark); }

.taskItem__btn--prev {
  left: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.taskItem__btn--next {
  right: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

/* 
================================================================================ MAIN > TASK ITEMS > TASK FORM
*/

.taskForm { text-align: right; }

.taskForm__heading { margin-bottom: .5rem; }

.taskForm .inputContainer { margin-bottom: 1rem; }

.taskForm .btn:not(:last-of-type) { margin-right: 0.3rem; }

.taskForm__input {
  min-height: 5rem;
  width: 100%;
  resize: none;
  margin-bottom: 1rem;
}

.dark .taskForm__input {
  color: var(--color-lightgrey);
  background-color: var(--color-darkgrey);
}

/* 
================================================================================ FOOTER
*/

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  color: var(--color-dark);
  font-size: 1.3rem;
  text-align: center;
  padding: 2rem 0;
  background-color: var(--color-light);
  box-shadow: 0 -2px 3px rgba(17,17,17,0.4);
}

.dark footer {
  background-color: var(--color-dark);
  color: var(--color-lightgrey);
}

footer .displayContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  row-gap: .3rem;
}

footer a {
  color: inherit;
  transition: color 0.3s;
}

footer a:hover { color: var(--color-red) }
.dark footer a:hover { color: var(--color-lightred) }

/* 
================================================================================ MEDIA QUERIES
*/

/* ------------------------------------ MAX-WIDTH 768px */
@media (max-width: 768px) {
  .header__buttons > .btn:first-child {
    margin-bottom: 5px;
  }
  
  .taskBoard__menu {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    justify-items: center;
    row-gap: 1rem;
  }

  
}

/* ------------------------------------ MAX-WIDTH 480px */
@media (max-width: 480px) {
  .btn { font-size: 1.2rem;}

  header .btn { justify-self: center; }
  header .displayContainer { 
    text-align: center; 
    justify-content: center;
  }
  .header__buttons { text-align: center; }
  
  .inputContainer__filter label:not(:last-of-type) { margin-right: .5rem; }
}